import { _decorator, Component, Node, SpriteFrame, Sprite } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('game_jxlw')
export class game_jxlw extends Component {

    //赢分、我的积分、比倍 用的数字
    @property(SpriteFrame)
    up_number_spf: SpriteFrame[] = [];

    //下注用的数字
    @property(SpriteFrame)
    dowm_number_spf: SpriteFrame[] = [];

    //赢分spr
    @property(Sprite)
    win_score: Sprite[] = [];



    start() {

    }

    update(deltaTime: number) {


    }

    //设置赢分
    setWinScore(score: number) {
        let str = score.toString();
        let len = str.length;
        console.log('str = ', str)
        console.log('len = ', len)
        for (let i = 0; i < 10; i++) {
            if (i > len) {
                this.win_score[i].node.active = false;
            }
            else {
                let aaa = str[0];
                console.log('aaaa = ', aaa)

                let st = Number(aaa);
                console.log('st11 = ', st)

                this.win_score[i].spriteFrame = this.up_number_spf[st];
                this.win_score[i].node.active = true;
            }
        }
    }

    aaaa = 1;
    onBtnClick(event: any, customEventData: any) {
        switch (customEventData) {
            case 'go':
                {
                    this.aaaa += 1;
                    this.setWinScore(this.aaaa);
                    break;
                }
            default:
                break;
        }
    }
}